@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  padding: 1rem;
  width: 100%;
  margin: auto;
  max-width: 1280px;
}

ol, ul {
  list-style: none;
}

a {
  text-decoration: none;
}


/* Footer Common Style */
footer .footer_title{
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

footer ul a{
  color: #bbb;
  font-weight: 300;
}

footer ul a:hover{
  color: #fff;
}